import React from "react";
import styles from "../styles/style";
import { discount, robot } from "../assets";
import { GetStarted } from "../components";
import { motion } from "framer-motion";

const Hero = () => {
  const animation = {
    hidden: {
      x: -100,
      opacity: 0
    },
    visible: custom => ({
      x: 0,
      opacity: 1,
      transition: { delay: custom * 0.2 }
    })
  };
  return (
    <motion.section
      initial="hidden"
      whileInView="visible"
      id="home"
      className={`flex md:flex-row flex-col ${styles.paddingY}`}
    >
      <div className={`flex-1 ${styles.flexStart} flex-col xl:px-0 sm:px-16 px-6`}>
        <motion.div
          custom={1}
          variants={animation}
          className="flex flex-row items-center py-[6px] px-4 bg-discount-gradient rounded-[10px] mb-2"
        >
          <img src={discount} alt="discount" className="w-[32px] h-[32px]" />
          <p className={`${styles.paragraph} ml-2`}>
            <span className="text-white">20% </span>
            Discount For <span className="text-white">1 Month </span>
            Account
          </p>
        </motion.div>

        <div className="flex flex-row justify-between items-center w-full">
          <motion.h1
            variants={animation}
            custom={2}
            className="flex-1 font-poppins font-semibold ss:text-[72px] text-[52px] text-white ss:leading-[100px] leading-[75px]"
          >
            The Next <br className="sm:block hidden" />
            <span className="text-gradient">Generation </span>
          </motion.h1>
          <motion.div
            variants={{
              hidden: {
                y: -100,
                opacity: 0
              },
              visible: {
                y: 0,
                opacity: 1,
                transition: { delay: 0.6 }
              }
            }}
            custom={2}
            className="ss:flex hidden md:mr-4 mr-0"
          >
            <GetStarted />
          </motion.div>
        </div>
        <motion.h1
          variants={animation}
          custom={2}
          className="font-poppins font-semibold ss:text-[68px] text-[52px] text-white ss:leading-[100px] leading-[75px] w-full"
        >
          Payment Method.
        </motion.h1>
        <motion.p
          variants={animation}
          custom={3}
          className={`${styles.paragraph} max-w-[470px] mt-5`}
        >
          Our team of experts uses a methodology to identify the credit cards most likely to fit
          your needs. We examine annual percentage rates, annual fees.
        </motion.p>
      </div>

      <div className={`flex-1 flex ${styles.flexCenter} md:my-0 my-10 relative`}>
        <motion.img
          variants={{
            hidden: {
              x: 100,
              opacity: 0
            },
            visible: {
              x: 0,
              opacity: 1,
              transition: { delay: 0.4 }
            }
          }}
          src={robot}
          alt="robotHand"
          className="w-[100%] h-[100%] relative z-[5]"
        />
        <div className="absolute z-[0] w-[40%] h-[35%] top-0 pink__gradient" />
        <div className="absolute z-[1] w-[80%] h-[80%] rounded-full bottom-40 white__gradient" />
        <div className="absolute z-[0] w-[50%] h-[50%] right-20 bottom-20 blue__gradient" />
      </div>

      <div className={`ss:hidden ${styles.flexCenter}`}>
        <GetStarted />
      </div>
    </motion.section>
  );
};

export default Hero;
